video {
  position: absolute;
  z-index: -10;

  width: 100%;
  @media screen and (max-width: 650px) {
    position: absolute;
    top: -200px;
    left: 0%;
    width: 1600px;
    height: 1000px;
  }
}

.home {
  height: 100vh;
  width: 100vw;
  position: absolute;

  &:after {
    content: "";
    height: 900px;
    width: 900px;
    background: $color-3;
    border-radius: 50%;
    position: absolute;
    bottom: -480px;
    right: 0px;
    z-index: -2;
    animation: homeAnim 15s infinite alternate;

    @media screen and (max-width: 650px) {
      height: 400px;
      width: 400px;
      bottom: -270px;
    }

    @keyframes homeAnim {
      50% {
        transform: scale(0.8);
      }
      100% {
        transform: scale(1.1);
      }
    }
    @media screen and (max-width: 430px) {
      display: none;
    }
  }

  .home-main {
    display: grid;
    align-items: center;
    justify-items: center;
    padding-left: 1rem;
    width: auto;
    height: 95%;
    overflow: hidden;

    .main-content {
      user-select: none;

      h1 {
        /* 1 pixel black shadow to left, top, right and bottom */
        text-shadow: -1px 0 rgb(109, 95, 95), 0 2px rgb(97, 91, 91), 1px 0 black,
          0 -1px rgb(109, 100, 100);

        font-family: sans;

        font-size: 3.7rem;
        position: relative;
        font-family: $font-2;
        color: rgba(255, 255, 255, 0.788);

        @media screen and (max-width: 650px) {
          font-size: 4rem;
        }
        @media screen and (min-width: 1200px) {
          font-size: 5rem;
        }

        &:before {
          content: "";
          position: absolute;
          height: 225px;
          width: 225px;
          border-radius: 50%;
          border: 1px $color-2 solid;
          top: -40px;
          left: -180px;
          z-index: -1;
          animation: h1Anim 1.5s forwards ease;
          transform: scale(0);

          @media screen and (max-width: 850px) {
            height: 110px;
            width: 110px;
            left: -40px;
            top: -15px;
          }

          @keyframes h1Anim {
            to {
              transform: scale(1);
            }
          }
        }
      }
      h2 {
        font-size: 4.2rem;
        font-weight: 100;

        @media screen and (max-width: 650px) {
          font-size: 2rem;
        }
        @media screen and (min-width: 1200px) {
          font-size: 5rem;
        }
      }
    }
  }
}
